import './styles/style.css';
import App from './App';
import loadingSVG from './assets/icons/loading.svg';

const loading = document.querySelector('.loading');
const img = document.createElement('img');
img.src = loadingSVG;
loading.appendChild(img);

const app = document.querySelector('#app');

init('vietnam', 'C');

function init(query, unit) {
  App({ query, unit }).then((html) => {
    app.innerHTML = html;
    addEvents();
  });
}

function addEvents() {
  const form = document.querySelector('form.search-box');
  const changeUnit = document.querySelector('#change-unit');
  form.addEventListener('submit', (e) => {
    e.preventDefault();
    init(e.target.location.value);
  });
  changeUnit.addEventListener('click', () => {
    if (changeUnit.dataset.unit === 'C') {
      init(null, 'F');
    } else {
      init(null, 'C');
    }
  });
}
